.App,
.mdisk,
.dood,
.home {
  text-align: center;
  background-color: #434645;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  width: 100%;
  color: white;
}

.mdisk {
  color: black;
  background-color: #e5e5e5;
  width: 100%;
}

#dood-element {
  border: none;
}
#mdisk-element {
  border: none;
}
.home {
  padding: 80px;
  /* opacity: 0.9; */
  background-image: url('https://res.cloudinary.com/ddvaiwvwr/image/upload/v1642938887/fdisk.in/bg1_ctssoa.png');
  background-position: center top;
  background-size: cover;
}
.home h3,
.home h4,
.home h5 {
  margin: 0;
  padding: 10px 20%;
}
.home h4 {
  color: #ff7700;
}
.home h5 {
  color: #b0b3b8;
  line-height: 1.5;
}
.home h3 {
  color: white;
  font-weight: bold;
  line-height: 1.5;
}
@media (max-width: 430px) {
  .home h3,
  .home h4,
  .home h5 {
    margin: 0;
    padding: 7px 7%;
  }
  .home {
    padding: 60px 10px;
  }
  .home h4 {
    font-size: 15.5px;
  }
  .home h3 {
    font-size: 17.5px;
  }
}
